<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghuzhanghao">
        <el-input v-model="ruleForm.zhanghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghuxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghushouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghudizhi">
        <el-input v-model="ruleForm.dizhi" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjiashangjiabianhao">
        <el-input v-model="ruleForm.shangjiabianhao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjiamima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjiashangjiamingcheng">
        <el-input v-model="ruleForm.shangjiamingcheng" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjialianxiren">
        <el-input v-model="ruleForm.lianxiren" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjialianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjiatese">
        <el-input v-model="ruleForm.tese" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='shangjia'" label="" prop="shangjiadizhi">
        <el-input v-model="ruleForm.dizhi" placeholder=""></el-input>
      </el-form-item>
                                                                                                      <el-form-item v-if="tableName=='qishou'" label="" prop="qishouyonghuming">
        <el-input v-model="ruleForm.yonghuming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qishou'" label="" prop="qishoumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qishou'" label="" prop="qishouqishouxingming">
        <el-input v-model="ruleForm.qishouxingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='qishou'" label="" prop="qishoudianhua">
        <el-input v-model="ruleForm.dianhua" placeholder=""></el-input>
      </el-form-item>
                                                                              <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                              if((!this.ruleForm.zhanghao) && `yonghu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `yonghu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                              if(`yonghu` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                                                                  if((!this.ruleForm.shangjiabianhao) && `shangjia` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `shangjia` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                              if(`shangjia` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                                                      if((!this.ruleForm.yonghuming) && `qishou` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `qishou` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                              if(`qishou` == this.tableName && this.ruleForm.dianhua&&(!this.$validate.isMobile(this.ruleForm.dianhua))){
        this.$message.error(``);
        return
      }
                                                                                                this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
