<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
      <el-row>
                  <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="mingcheng">
          <el-input v-model="ruleForm.mingcheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.mingcheng" label="" prop="mingcheng">
              <el-input v-model="ruleForm.mingcheng" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="type!='info'" label="" prop="tupian">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.tupian?ruleForm.tupian:''"
          @change="tupianUploadChange"
          ></file-upload>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.tupian" label="" prop="tupian">
            <img style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="item" width="100" height="100">
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="goumaishuliang">
          <el-input v-model="ruleForm.goumaishuliang" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.goumaishuliang" label="" prop="goumaishuliang">
              <el-input v-model="ruleForm.goumaishuliang" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="shangjiabianhao">
          <el-input v-model="ruleForm.shangjiabianhao" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.shangjiabianhao" label="" prop="shangjiabianhao">
              <el-input v-model="ruleForm.shangjiabianhao" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="zhanghao">
          <el-input v-model="ruleForm.zhanghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.zhanghao" label="" prop="zhanghao">
              <el-input v-model="ruleForm.zhanghao" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="beizhu">
          <el-input v-model="ruleForm.beizhu" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.beizhu" label="" prop="beizhu">
              <el-input v-model="ruleForm.beizhu" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="qishoushouyi">
          <el-input v-model="ruleForm.qishoushouyi" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.qishoushouyi" label="" prop="qishoushouyi">
              <el-input v-model="ruleForm.qishoushouyi" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'" label="" prop="jiedanshijian">
            <el-date-picker
                value-format="yyyy-MM-dd HH:mm:ss"
                v-model="ruleForm.jiedanshijian" 
                type="datetime"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.jiedanshijian" label="" prop="jiedanshijian">
              <el-input v-model="ruleForm.jiedanshijian" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="yonghuming">
          <el-input v-model="ruleForm.yonghuming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yonghuming" label="" prop="yonghuming">
              <el-input v-model="ruleForm.yonghuming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="qishouxingming">
          <el-input v-model="ruleForm.qishouxingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.qishouxingming" label="" prop="qishouxingming">
              <el-input v-model="ruleForm.qishouxingming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="dianhua">
          <el-input v-model="ruleForm.dianhua" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.dianhua" label="" prop="dianhua">
              <el-input v-model="ruleForm.dianhua" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'"  label="" prop="dingdanzhuangtai">
          <el-select v-model="ruleForm.dingdanzhuangtai" placeholder="">
            <el-option
                v-for="(item,index) in dingdanzhuangtaiOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.dingdanzhuangtai" label="" prop="dingdanzhuangtai">
              <el-input v-model="ruleForm.dingdanzhuangtai" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                                                                                                                    <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="fulladdress">
          <el-input   @click.native="openMapDiolag"  v-model="ruleForm.fulladdress" 
              placeholder=""></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.fulladdress" label="" prop="fulladdress">
              <el-input v-model="ruleForm.fulladdress" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
            </el-row>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <el-form-item>
                <el-button v-if="type!='info'" type="primary" @click="onSubmit"></el-button>
        <el-button v-if="type!='info'" @click="back()"></el-button>
        <el-button v-if="type=='info'" @click="back()"></el-button>
      </el-form-item>
    </el-form>
    
        <el-dialog
      width="50%"
      title=""
      :visible.sync="mapVisiable"
      append-to-body>
      <el-amap-search-box class="search-box" :on-search-result="onSearchResult" :search-option="searchOption" ></el-amap-search-box>
      <div class="amap-wrapper">
        <el-amap class="amap-box" :vid="'amap-vue'" 
        :center="center"
        :zoom="zoom"
        :events="events" >
          <el-amap-marker v-bind:key="marker" v-for="marker in markers" :position="marker" ></el-amap-marker>
        </el-amap>
      </div>
      <div>[{{ ruleForm.longitude }}, {{ ruleForm.latitude }}],{{ruleForm.fulladdress}}</div>
    </el-dialog>
    
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    let self = this
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',
            // 
      mapVisiable: false,
      zoom: 12,
      center: [116.410426, 39.934946],
      markers: [],
      searchOption: {
        citylimit: false
      },
      events: {
        click (e) {
          let { lng, lat } = e.lnglat
          self.ruleForm.longitude = lng
          self.ruleForm.latitude = lat
          //  SDK 
          /* eslint-disable */
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: 'all'
          })
          geocoder.getAddress([lng, lat], function (status, result) {
            if (status === 'complete' && result.info === 'OK') {
              if (result && result.regeocode) {
                console.log(result)
                self.ruleForm.fulladdress = result.regeocode.formattedAddress
              }
            }
          })
        }
      },
            ruleForm: {
                        mingcheng: '',
                                tupian: '',
                                goumaishuliang: '',
                                shangjiabianhao: '',
                                zhanghao: '',
                                beizhu: '',
                                qishoushouyi: '',
                                jiedanshijian: '',
                                yonghuming: '',
                                qishouxingming: '',
                                dianhua: '',
                                dingdanzhuangtai: '',
                                sfsh: '',
                                shhf: '',
                                ispay: '',
                                longitude: '',
                                latitude: '',
                                fulladdress: '',
                      },
                                                                                                                                                                                  dingdanzhuangtaiOptions: [],
                                                                                                        rules: {
                  mingcheng: [
                                                                                              ],
                  tupian: [
                                                                                              ],
                  goumaishuliang: [
                                                                                              ],
                  shangjiabianhao: [
                                                                                              ],
                  zhanghao: [
                                                                                              ],
                  beizhu: [
                                                                                              ],
                  qishoushouyi: [
                                        { validator: validateIntNumber, trigger: 'blur' },
                                                                                  ],
                  jiedanshijian: [
                                                                                              ],
                  yonghuming: [
                                                                                              ],
                  qishouxingming: [
                                                                                              ],
                  dianhua: [
                                                                                              ],
                  dingdanzhuangtai: [
                                                                                              ],
                  sfsh: [
                                                                                              ],
                  shhf: [
                                                                                              ],
                  ispay: [
                                                                                              ],
                  longitude: [
                                                    { validator: validateNumber, trigger: 'blur' },
                                                                      ],
                  latitude: [
                                                    { validator: validateNumber, trigger: 'blur' },
                                                                      ],
                  fulladdress: [
                                                                                              ],
              }
    };
  },
  props: ["parent"],
  computed: {
                                                                                                                                                                                                                              },
  methods: {
        // 
    openMapDiolag () {
      this.mapVisiable = true
    },
    // 
    onSearchResult (pois) {
      let latSum = 0;
      let lngSum = 0;
      if (pois.length > 0) {
        pois.forEach(poi => {
          let {lng, lat} = poi;
          lngSum += lng;
          latSum += lat;
          this.markers.push([poi.lng, poi.lat]);
        });
        let center = {
          lng: lngSum / pois.length,
          lat: latSum / pois.length
        };
        this.center = [center.lng, center.lat];
      }
    },
        // 
    download(file){
      window.open(`${file}`)
    },
    // 
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
                    if(o=='mingcheng'){
            this.ruleForm.mingcheng = obj[o];
            continue;
          }
                    if(o=='tupian'){
            this.ruleForm.tupian = obj[o];
            continue;
          }
                    if(o=='goumaishuliang'){
            this.ruleForm.goumaishuliang = obj[o];
            continue;
          }
                    if(o=='shangjiabianhao'){
            this.ruleForm.shangjiabianhao = obj[o];
            continue;
          }
                    if(o=='zhanghao'){
            this.ruleForm.zhanghao = obj[o];
            continue;
          }
                    if(o=='beizhu'){
            this.ruleForm.beizhu = obj[o];
            continue;
          }
                    if(o=='qishoushouyi'){
            this.ruleForm.qishoushouyi = obj[o];
            continue;
          }
                    if(o=='jiedanshijian'){
            this.ruleForm.jiedanshijian = obj[o];
            continue;
          }
                    if(o=='yonghuming'){
            this.ruleForm.yonghuming = obj[o];
            continue;
          }
                    if(o=='qishouxingming'){
            this.ruleForm.qishouxingming = obj[o];
            continue;
          }
                    if(o=='dianhua'){
            this.ruleForm.dianhua = obj[o];
            continue;
          }
                    if(o=='dingdanzhuangtai'){
            this.ruleForm.dingdanzhuangtai = obj[o];
            continue;
          }
                    if(o=='sfsh'){
            this.ruleForm.sfsh = obj[o];
            continue;
          }
                    if(o=='shhf'){
            this.ruleForm.shhf = obj[o];
            continue;
          }
                    if(o=='ispay'){
            this.ruleForm.ispay = obj[o];
            continue;
          }
                    if(o=='longitude'){
            this.ruleForm.longitude = obj[o];
            continue;
          }
                    if(o=='latitude'){
            this.ruleForm.latitude = obj[o];
            continue;
          }
                    if(o=='fulladdress'){
            this.ruleForm.fulladdress = obj[o];
            continue;
          }
                  }
                                                                                                                                                                                                                                                                                                              }
            // 
      this.$http({
        url: `${this.$storage.get('sessionTable')}/session`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          var json = data.data;
                                                                                                                                                                                                                    this.ruleForm.yonghuming = json.yonghuming
                                                this.ruleForm.qishouxingming = json.qishouxingming
                                                this.ruleForm.dianhua = json.dianhua
                                                                                                                                                                                        } else {
          this.$message.error(data.msg);
        }
      });
                                                                                                                                                                                                                                                this.dingdanzhuangtaiOptions = ",,".split(',')
                                                                                                                                        },
                                                                                                                                                        // 
                                                                                                                                                                                                info(id) {
      this.$http({
        url: `peisongdan/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
        // 
    onSubmit() {
                  // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                                                                                                                                                                                                                                                                                                                                                                        this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `peisongdan/${!this.ruleForm.id ? "save" : "update"}`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = true;
                  this.parent.addOrUpdateFlag = false;
                  this.parent.peisongdanCrossAddOrUpdateFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.peisongdanCrossAddOrUpdateFlag = false;
    },
                                    tupianUploadChange(fileUrls) {
                this.ruleForm.tupian = fileUrls;
            },
                                                                                                                                                                                                              }
};
</script>
<style lang="scss">
.editor{
  height: 500px;
}
.amap-wrapper {
  width: 100%;
  height: 500px;
}
.search-box {
  position: absolute;
}
</style>
